wrap;\n    }\n    .logo {\n      width: 240px;\n      height: 68px;\n      padding: 0 8px;\n      box-sizing: border-box;\n      text-align: center;\n      line-height: 72px;\n      font-size: 18px;\n      color: #333333;\n      margin-bottom: 10px;\n    }\n    .user_box {\n      display: flex;\n      flex-direction: column;\n      align-items: center;\n      .img_box {\n        width: 120px;\n        height: 120px;\n        border-radius: 50%;\n        margin-bottom: 16px;\n        overflow: hidden;\n        :deep(.el-image) {\n          height: 100%;\n          .el-image__inner {\n            object-fit: cover;\n          }\n        }\n        img {\n          width: 100%;\n          height: 100%;\n          object-fit: cover;\n        }\n      }\n      .name {\n        width: 160px;\n        overflow: hidden;\n        text-overflow: ellipsis;\n        white-space: nowrap;\n        height: 16px;\n        font-size: 16px;\n        color: #333333;\n        letter-spacing: 1px;\n        line-height: 16px;\n        font-weight: 700;\n        margin-bottom: 16px;\n        text-align: center;\n      }\n      .res_day {\n        height: 12px;\n        font-size: var(--cur-font-size-min);\n        color: #666666;\n        line-height: 12px;\n        font-weight: 400;\n        margin-bottom: 24px;\n        text-align: center;\n      }\n    }\n  }\n\n  // 底部操作\n  .bottom_opt_box {\n    background-image: linear-gradient(\n      180deg,\n      rgba(241, 250, 254, 0) 0%,\n      rgba(241, 250, 254, 0.8) 10%,\n      #f3fbfd 45%\n    );\n\n    padding-top: 35px;\n    position: absolute;\n    left: 0;\n    bottom: 0;\n    width: 240px;\n  }\n  .actions {\n    padding: 24px 16px;\n    margin: 16px 24px 30px 24px;\n    max-height: 70px;\n    background-image: linear-gradient(\n      180deg,\n      #ffffff 0%,\n      rgba(255, 255, 255, 0.48) 100%\n    );\n    border: 2px solid rgba(255, 255, 255, 1);\n    border-radius: 6px;\n    overflow: hidden;\n    cursor: pointer;\n    box-sizing: border-box;\n    transition: max-height 0.2s ease-in;\n\n    // &:hover {\n    //   max-height: 999px;\n    //   // .btn-edit,\n    //   // .btn-logout {\n    //   //   display: block;\n    //   // }\n    // }\n    .hello {\n      display: flex;\n      align-items: center;\n      gap: 8px;\n      margin-bottom: 24px;\n      justify-content: space-between;\n      .tt {\n        font-size: 12px;\n        color: var(--cur-font-color-min);\n        font-size: 12px;\n        padding-top: 2px;\n        display: flex;\n        justify-content: space-between;\n      }\n\n      .name {\n        font-size: 14px;\n        color: #3a3a3d;\n        font-size: 14px;\n      }\n      .el-icon {\n        color: var(--cur-font-color-min);\n      }\n      // .iconfont {\n      //   color: #3a3a3d !important;\n      //   margin-left: auto;\n      // }\n    }\n\n    .btn-edit {\n      margin-bottom: 8px;\n    }\n\n    .btn-edit,\n    .btn-logout {\n      // display: none;\n      font-size: 14px;\n      color: #7d7d84;\n      padding: 8px;\n\n      &:hover {\n        color: #5f80ff;\n        background: #f1f4ff;\n        border-radius: 8px;\n      }\n\n      .iconfont {\n        margin-right: 8px;\n      }\n    }\n  }\n  // 路由盒子\n  .router_box {\n    padding: 30px 24px;\n    flex: 1 1 0;\n    overflow: hidden;\n  }\n  .openIndex {\n    width: 192px;\n    height: 50px;\n    border-radius: 6px;\n    margin: 0 auto;\n    padding: 15px 24px;\n    display: flex;\n    box-sizing: border-box;\n    background: url('@/assets/openBG.png');\n    background-repeat: no-repeat;\n    background-size: 100%;\n    font-size: var(--cur-font-size-lig);\n    color: var(--cur-font-color);\n    letter-spacing: 0;\n    display: flex;\n    align-items: center;\n    justify-content: space-between;\n    background-color: white;\n    cursor: pointer;\n    > svg {\n      cursor: pointer;\n    }\n    p {\n      height: 14px !important;\n    }\n  }\n"],"sourceRoot":""}]);
